.Hero{
    // margin-top: 200px;
    width: 100%;
    height: 600px;
    // border: 3px solid red;
    position: relative;
    top: 100px;
    background-color: #0EB4C1;

    .hero__wrapper{
        width: 50%;
        height: 100%;
        // border: 2px solid blue;
        .hero__item{
            width: 50%;
            h1{
                width: 650px;
                height: 80px;
                font-weight: 700;
                font-size: 36px;
                padding-top: 100px;
                padding-left: 170px;
                color: white;
            }
            p{
                width: 440px;
                height: 108px;
                margin-top: 200px;
                margin-left: 170px;
                font-weight: 400;
                font-size: 18px;
                line-height: 27px;
                color: white;
                
            }
           
            button{
                width: 189px;
                height: 47px;
                margin-top: 30px;
                margin-left: 170px;
                border-radius: 8px;
                border: none;
                background-color: #DE2E54;
                font-size: 15px;
                color: white;
                line-height: 22.5px;

                :hover{
                    color: #DE2E54;
                    background-color: white;
                }
            }
            img{
                margin-top: -400px;
                margin-left: 830px;
            }

        }
    }

    
}