header{
    position: fixed;
    width: 100%;
    padding: 1rem;
    background-color: white;

    nav{
        // padding: 22px 0px;
        display: flex;
       align-items: center;
       max-width: 1200px;
       margin: 0 auto;
       justify-content: space-between;


       ul{
        display: flex;
        align-items: center;
        gap: 1rem;
        .text{
            :focus{
                color: #DE2E54;
            }
        }

        li{
            list-style: none;

            

            a{
                text-decoration: none;
                color: black;

               
            }
            .btn{
                :hover{
                    background-color: white;
                    color: #DE2E54;
                    border: 1px solid #DE2E54;
                }
            }
            button{
                width: 189px;
                height: 47px;
                background-color: #DE2E54;
                border-radius: 10px;
                border: none;
                color: white;
                font-weight: 700;

                

                
            }
            
            
        }
       }
    }
}